import {
  Paper,
  Stack,
  Typography,
  TableHead,
  TableBody,
  TableContainer,
  Table,
  TableRow,
  TableCell,
  TablePagination
} from "@mui/material";

import { useMemo, useState } from "react";
import { Link } from "react-router-dom";
import SymptomsFilter from "../components/Symptom/SymptomsFilter";

import usePost from "../hooks/usePost";
import useStyles from "../hooks/useStyles";
import { Delete } from "@mui/icons-material";

const Symptoms = ({ resource }) => {
  document.title = "Symptoms";
  const allSymptoms = resource.read();
  const { sxHeaders: headers, fieldStyles, headerStyles } = useStyles();
  const { deleteSymptom } = usePost();
  const [filter, setFilter] = useState("Show all");
  const [page, setPage] = useState(0);
  const [rowsPerPage, setRowsPerPage] = useState(10);

  const displayedSymptoms = useMemo(() => {
    const combinedSymptoms =
      filter === "Show all"
        ? Object.values(allSymptoms)
            .flat()
            .map(s => Object.values(s))
            .flat()
        : Object.values(allSymptoms[filter] || {});

    return combinedSymptoms
      .sort((a, b) => a.medical_term.localeCompare(b.medical_term))
      .slice(page * rowsPerPage, (page + 1) * rowsPerPage)
      .map(symptom => ({ ...symptom, tags: symptom.tags }));
  }, [allSymptoms, filter, page, rowsPerPage]);

  const totalSymptomsCount = useMemo(() => {
    const combinedSymptoms =
      filter === "Show all" ? Object.values(allSymptoms) : Object.keys(allSymptoms[filter]);

    const combinedValues = combinedSymptoms.map(s => Object.values(s)).flat();

    return filter === "Show all" ? combinedValues.length : combinedSymptoms.length;
  }, [allSymptoms, filter]);

  const handleChangeRowsPerPage = event => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  async function onDelete(id) {
    const answer = confirm("Are you sure you want to delete this symptom?");
    if (answer) {
      await deleteSymptom(id);
      resource.mutate(data => {
        const newData = { ...data };
        Object.keys(newData).forEach(key => {
          newData[key] = newData[key].filter(sx => sx.id !== id);
        });
        return newData;
      }, false);
    }
  }

  return (
    <Stack direction="column" sx={{ width: "100%", height: "100vh", backgroundColor: "#fff" }}>
      <SymptomsFilter filter={filter} setFilter={setFilter} setPage={setPage} />
      <Paper sx={{ width: "100%" }}>
        <TableContainer>
          <Table size="small" aria-label="Requests Table">
            <TableHead>
              <TableRow>
                {headers.map(header => (
                  <TableCell key={header} sx={headerStyles}>
                    {header === "Symptom" ? `Symptom (${totalSymptomsCount})` : header}
                  </TableCell>
                ))}
              </TableRow>
            </TableHead>
            <TableBody>
              {displayedSymptoms.map(sxObj => (
                <TableRow key={sxObj.id}>
                  <TableCell>
                    <Link to={`${sxObj.id}`} style={{ textDecoration: "none" }}>
                      <Typography
                        sx={{
                          ...fieldStyles,
                          cursor: "pointer",
                          "&:hover": { color: "blue" }
                        }}>
                        {sxObj["medical_term"]}
                      </Typography>
                    </Link>
                  </TableCell>
                  <TableCell>
                    <Typography sx={fieldStyles}>{sxObj["symptom"]}</Typography>
                  </TableCell>
                  <TableCell>
                    <Typography
                      onClick={() => handleTagClick(sxObj)}
                      sx={{
                        ...fieldStyles,
                        cursor: "pointer",
                        "&:hover": { color: "blue" },
                        fontSize: "10px"
                      }}>
                      {sxObj["tags"] ? sxObj.tags : "Add"}
                    </Typography>
                  </TableCell>
                  <TableCell>
                    <Delete
                      onClick={() => onDelete(sxObj.id)}
                      sx={{ cursor: "pointer", fontSize: "16px", "&:hover": { color: "red" } }}
                    />
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
          <TablePagination
            component="div"
            count={totalSymptomsCount}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
            labelRowsPerPage="Rows per page"
            rowsPerPageOptions={[10, 25, 50]}
          />
        </TableContainer>
      </Paper>
    </Stack>
  );
};

export default Symptoms;
