import { FormControl, Autocomplete, TextField } from "@mui/material";

const SymptomsFilter = ({ filter, setFilter, setPage }) => {
  const options = [
    "Show all",
    "Constitutional/General",
    "Eyes",
    "Ear/Nose/Throat",
    "Cardiovascular",
    "Respiratory",
    "Gastrointestinal",
    "Genitourinary",
    "Gynecological",
    "Peripheral (extremities)",
    "Hematologic/Lymphatic",
    "Hormonal/Endocrinological",
    "Musculoskeletal",
    "Neurological",
    "Psychiatric",
    "Sexual history",
    "Past Medical History",
    "Skin",
    "Other"
  ];

  const handleFilterChange = (_, value) => {
    console.log(value);
    setFilter(value || "Show all");
    setPage(0);
  };

  const control = {
    value: filter,
    onChange: handleFilterChange,
    exclusive: true
  };

  return (
    <FormControl sx={{ width: "90%" }}>
      <Autocomplete
        disablePortal
        autoHighlight
        size="small"
        id="symptoms-filter"
        value={filter}
        options={options}
        renderInput={params => (
          <TextField
            {...params}
            label="Symptoms filter"
            sx={{
              fontFamily: "Poppins",
              color: "#36454f",
              marginY: "5px",
              fontSize: "12px"
            }}
            value={filter}
          />
        )}
        onChange={(e, newValue) => handleFilterChange(e, newValue)}
      />
    </FormControl>
  );
};
export default SymptomsFilter;
